@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  font-family: "Kanit", sans-serif;
}

::-webkit-scrollbar-track {
  /* -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0.3vh;
}

/* Set the scrollbar width */
::-webkit-scrollbar {
  width: 0.6vh;
}

/* Style the scrollbar thumb with rounded corners */
::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 0.3vh; /* Adjust the value to control the roundness */
}

.btn-3d {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(
    100% 100% at 100% 0px,
    rgb(0, 255, 225) 0px,
    rgb(19, 175, 162) 100%
  );

  border: 0px;
  border-radius: 6px;
  box-shadow: rgba(45, 35, 66, 0.4) 0px 2px 4px,
    rgba(45, 35, 66, 0.3) 0px 7px 13px -3px,
    rgba(58, 65, 111, 0.5) 0px -3px 0px inset;
  box-sizing: border-box;
  color: rgb(255, 255, 255);
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  line-height: 55px;
  overflow: hidden;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  text-decoration: none;
  transition: box-shadow 0.15s ease 0s, transform 0.15s ease 0s;
  user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
}
.btn-3d:active {
  box-shadow: rgba(15, 118, 110, 1) 0px 3px 7px inset;
  transform: translateY(2px);
}
